import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Geolocation } from '@ionic-native/geolocation';
import { GoogleMaps } from '@ionic-native/google-maps';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireOfflineModule } from 'angularfire2-offline';

import { DataService } from '../providers/data.service';
import { SQLite } from '@ionic-native/sqlite';
import { SqlService } from '../providers/sql.service';
import { LoginService } from '../providers/login.service';

var configFirebase = {
  apiKey: "AIzaSyAJDgpY9ssAyjIX_vA7S1D_fgcA26BBvxQ",
  authDomain: "svmmoviltest.firebaseapp.com",
  databaseURL: "https://svmmoviltest.firebaseio.com",
  projectId: "svmmoviltest",
  storageBucket: "svmmoviltest.appspot.com",
  messagingSenderId: "1094384829553"
};

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/adm-super/adm-super.module#AdmSuperPageModule', name: 'AdmSuperPage', segment: 'adm-super', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auto-venta/auto-venta.module#AutoVentaPageModule', name: 'AutoVentaPage', segment: 'auto-venta', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/create/create.module#CreatePageModule', name: 'CreatePage', segment: 'create', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/create-user/create-user.module#CreateUserPageModule', name: 'CreateUserPage', segment: 'create-user', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info/info.module#InfoPageModule', name: 'InfoPage', segment: 'info', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info-map/info-map.module#InfoMapPageModule', name: 'InfoMapPage', segment: 'info-map', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list/list.module#ListPageModule', name: 'ListPage', segment: 'list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-user/list-user.module#ListUserPageModule', name: 'ListUserPage', segment: 'list-user', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map/map.module#MapPageModule', name: 'MapPage', segment: 'map', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map-generic/map-generic.module#MapGenericPageModule', name: 'MapGenericPage', segment: 'map-generic', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/preventa/preventa.module#PreventaPageModule', name: 'PreventaPage', segment: 'preventa', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp( configFirebase ),
    AngularFireDatabaseModule,
    AngularFireOfflineModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Geolocation,
    GoogleMaps,
    SQLite,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DataService,
    SqlService,
    LoginService
  ]
})
export class AppModule {}



